import Client from './Client'
const qs = require('query-string')

class SDK {

	constructor() {
		const params = qs.parse(location.search)
		if(!params.iid) {
			throw new Error('esca SDK has not been properly configured through the esca Dashboard.')
		}

		this.iid = params.iid
		this.authenticate()
	}

	authenticate() {
		Client.test()
	}

}

export default SDK


